import React from "react";
import comment from '../../constants/comments.json';
const Comments = () => {
  return (<>
{comment.map((e,i)=>(
<div className="media commnets" key={i}>
						<a className="pull-left" href="#">
							<img className="media-object" src={e.image} alt="image"/>
						</a>
						<div className="media-body">
							<h4 className="media-heading">{e.user}</h4>
                            <p>{e.comment}</p>
							<div className="blog-socials">
								<ul>
									<li><a href=""><i className="fa fa-facebook"></i></a></li>
									<li><a href=""><i className="fa fa-twitter"></i></a></li>
									<li><a href=""><i className="fa fa-dribbble"></i></a></li>
									<li><a href=""><i className="fa fa-google-plus"></i></a></li>
								</ul>
								<a className="btn btn-primary" href="">Other Posts</a>
							</div>
						</div>
					</div>
                    ))}
  </>);

}
export default Comments;
