import axios from 'axios';

export const Server = (methodType,endpoint, payload,actionType ) => {
    return async dispatch =>{
        dispatch({
            type : "PRODUCT_GET_STARTED",
        }) 
        await axios({
            method: methodType,
            url: endpoint,
            data: {
              ...payload
            }
          })
        .then(res=> {         
            let data =res.data;                                        
            dispatch({
                type : actionType,
                data:data
            })
        }).catch(err=> {
            dispatch({
                type : "PRODUCT_FETCH_FAILED",
                data:err
            })
        })
             
    }
}