import React from "react";
import response from '../../constants/responses.json'; 
const Responses = () => {
  return (<>
  
<div className="response-area">
	<h2>{response.length} RESPONSES</h2>
    {response.map((e,i)=>(
    <ul className="media-list" key={i}>
							<li className="media">
								
								<a className="pull-left" href="#">
									<img className="media-object" src={e.image} alt=""/>
								</a>
								<div className="media-body">
									<ul className="sinlge-post-meta">
										<li><i className="fa fa-user"></i>{e.user}</li>
										<li><i className="fa fa-clock-o"></i>{e.time}</li>
										<li><i className="fa fa-calendar"></i>{e.date}</li>
									</ul>
                                   <p>{e.comment}</p>
									<a className="btn btn-primary" href=""><i className="fa fa-reply"></i>Replay</a>
								</div>
							</li>
                           
                    </ul>
			 ))}						
</div>
   
  
  </>);

}
export default Responses;
